.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-width: 600px;
  padding: 0 15px;
  margin: 0 auto;
  text-align: center;
}

.app__title {
  margin: 60px 0 0 0;
}

.app__list-count {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.4);
  margin-bottom: 40px;
}

.add-item-btn {
  background-color: #00a8ff;
  border: none;
  color: white;
  font-size: 18px;
  border-radius: 4px;
  height: 50px;
  border: 1px solid #0086cc;
  cursor: pointer;
  -webkit-transition: border 0.2s, background-color 0.2s, -webkit-transform 0.2s;
  transition: border 0.2s, background-color 0.2s, -webkit-transform 0.2s;
  -o-transition: border 0.2s, background-color 0.2s, transform 0.2s;
  transition: border 0.2s, background-color 0.2s, transform 0.2s;
  transition: border 0.2s, background-color 0.2s, transform 0.2s, -webkit-transform 0.2s;
  outline: none;
}

.add-item-btn:hover,
.add-item-btn:focus {
  background-color: #32b9ff;
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
}



